import noData from '@/components/noData'
import back from '@/components/back'
import { setInterval } from 'timers';

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
            activeTab: 0,
            med: [],
            search: '',
            pres: [],
			list: [],
            user: this.$store.state.userInfo,
			Patient: this.$store.state.patient,
			orderInfo: this.$store.state.pInfo,
            page: 1,
            haveMore: true,
            showConfirm: false,
            showConfirm2: false,
            delPreId: null,
            delPreIdx: null,
            delMedId: null,
            delMedIdx: null,
            ListDetails: null,
            removeMedI: null,
            removeMedIdx: null,
            showConfirm3: false,
			showConfirm4: false,
            pageShow: false,
            medSearchList: null,
            inputFlag: false,
			defaultImg: 'this.src="' + require('../../assets/images/prescriptionList.png') + '"',
			medSearchList2:null,
			medBlurSH:false,
			isShowXC:this.$store.state.isShowXC,
			pharmacyId:null,
			checkIdshow:2
        }
    },
    watch: {
        activeTab(){
            this.page = 1
            this.haveMore = true
            this.pageShow = false
            this.pull()
        }
    },
    computed:{
        totalPrice(){
            let p = 0
			if(this.ListDetails){
				if(this.ListDetails.length){
				    this.ListDetails.forEach(v => {
				        p += (+v.Price) * v.Quantity
				    })
				}
			}
            return p.toFixed(2)
        }
    },
    created(){
        if(this.$store.state.isUp){
            this.activeTab = this.$store.state.isUp
        }
        this.pull(false)
    },
    mounted(){
		if(this.$route.query.back){
			this.checkIdshow = 2
		}else{
			this.checkIdshow = 1
		}
		const { pullFlag } = this.$store.state
		if(pullFlag == 0 || !pullFlag){
		    this.getTemporary()
		}
		this.$bridge.callhandler('printdata','默认初始状态'+pullFlag)
        this.getMore()
        this.$refs.back.zIndex = false
    },
    beforeDestroy(){
        this.$store.dispatch('pushisUp',this.activeTab)
        window.onscroll = null
    },
    methods: {
		getTemporary(){  //获取草稿箱数据
			this.$bridge.callhandler('printdata','进入')
		    this.$post('Prescription/Prescription/GetTempPrescription',{
		        PatientId:this.orderInfo.PatientInfo.PatientId,
		        DoctorId: this.user.Data.User.DoctorId,
		    }).then(o => {
				this.$bridge.callhandler('printdata','数据'+o)
		        if(o){
		            this.showConfirm4 = true
		        }
		    })
		},
		confirm4(){
			this.$store.dispatch('pushpullFlag', 1)
			this.$store.dispatch('pushpullTemp', 1)
			this.$router.push(`/prescription`)
		},
		goBack(){
			if(this.$route.query.from === 'p' || this.$route.query.from === 'ump'){
				this.$router.push(`/prescription`)
			}
			else if(this.$route.query.from === 'up' || this.$route.query.from === 'upp'){
			    this.$router.push(`/usup-detail`)
			}
			else{
			    this.$router.push(`/prescription`)
			}
		},
		pullByDrId(){
			this.$get('Prescription/Pharmacy/GetMedicationCompanyByDrId',{
				DoctorId: this.user.Data.User.DoctorId,
			}).then(o => {
				this.pharmacyId = o.Id
				this.$bridge.registerhandler('PharmacyID', (data, responseCallback) => {
					this.$bridge.callhandler('printdata','app'+data)
					if(Number(data) >= 0){
						if(this.pharmacyId != Number(data)){
							this.$store.dispatch('pushpreNo', null)
							this.$store.dispatch('pushupList', [])
							this.ListDetails = []
							this.pull(false)
						}
					}
				    responseCallback('接收到方法'+data)
				})
			})
		},
		medBlur(){
            let that = this;
			setTimeout(function(){
                that.medBlurSH = false
            },100)
		},
		medInputMethods(){
			if(localStorage.getItem('searchName')){
				this.medSearchList2 = JSON.parse(localStorage.getItem('searchName')).slice(0,10)
				this.medBlurSH = true
			}
		},
        addTo(i){
            this.$store.dispatch('pushcMed', i)
            this.$router.push('/consumption?back=-2')
        },
        searchByName(){
            if(this.inputFlag){
                return
            }
            if(this.search){
				this.$post('Prescription/Medication/GetMedicationByNameForOftenUsed',this.$pick({
					MedicationName: this.search,
					OrgId: this.user.Data.User.OrgId,
					DoctorId: this.user.Data.User.DoctorId,
					PageIndex: this.page,
					PageSize: 20
				})).then(o => {
					this.medSearchList = o.ReturnData
				})
			}
        },
        toAddPre(){
            this.$store.dispatch('pushupList', [])
            this.$store.dispatch('pushDiagnoses', null)
            this.$router.push('/usup-detail')
        },
        confirm(){
            this.$post('Prescription/Prescription/DelPrescriptionOftenUsed',{
                Id: this.delPreId
            }).then( o => {
                this.pres.splice(this.delPreIdx, 1)
            })
        },
        confirm2(){
            this.$post('Prescription/Medication/DelMedicationOftenUsed',{
                Id: this.delMedId
            }).then( o => {
                this.med.splice(this.delMedIdx, 1)
                if(this.med.length < 15){
                    this.page = 1
                    this.pull(false)
                }
            })
        },
		toPre(i){
// 		    this.$post('Prescription/Pharmacy/SetMyMedicationCompany',{
// 		        MedicationCompanyId: i.MedicationCompanyId,
// 		        DoctorId: this.user.Data.User.DoctorId,
// 		    }).then(o => {
// 		        this.$store.dispatch('pushpreNo',null)
// 				this.$store.dispatch('pushpullFlag', 0)
// 		        this.$store.dispatch('pushDiagnoses2', i.Diagnoses)
// 		        this.$store.dispatch('pushupList', i.ListDetails)
// 		        this.$router.push(`/prescription?rejectId=${i.Id}`)
// 		    })
				this.$store.dispatch('pushpreNo',null)
				this.$store.dispatch('pushpullFlag', 0)
		        this.$store.dispatch('pushDiagnoses2', i.Diagnoses)
		        this.$store.dispatch('pushupList', i.ListDetails)
		        this.$router.push(`/prescription?rejectId=${i.Id}`)
		},
        searchFor(){
			if(!localStorage.getItem('searchName')){
				var searchArr = []
			}else{
				var searchArr = JSON.parse(localStorage.getItem('searchName'))
			}
			if(searchArr.indexOf(this.search) == -1 && this.search){
				searchArr.unshift(this.search)
			}
			localStorage.setItem('searchName', JSON.stringify(searchArr))

            this.$store.dispatch('pushsearchText',this.search)
            if(this.$route.query.from == 'p'){
                this.$router.push(`/medicine-list?id=&from=ump`)
            }
            else if(this.$route.query.from == 'up'){
                this.$router.push(`/medicine-list?id=&from=upp`)
            }
            else{
                this.$router.push(`/medicine-list?id=&from=um`)
            }
        },
        toChangePharmacy(){
            this.$bridge.callhandler('changePharmacy')
        },
        pull(h){
            if(this.activeTab == 0 && this.haveMore){
                this.$post('Prescription/Medication/GetMedicationOftenUsed',{
                    DoctorId:this.user.Data.User.DoctorId,
                    OrgId:this.user.Data.User.OrgId,
                    MedicationName: this.search,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.pageShow = true
                    this.ListDetails = this.$store.state.upList
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }

                    if (h){
                        const list = this.$store.state.upList
                        if(list){
                            o.ReturnData.forEach(v => {
                                list.forEach(a => {
                                    if(v.Name === a.Name && v.Brand === a.Brand && v.Manufacturer == a.Manufacturer){
                                        v.state = 'onUp'
                                    }
                                })
                                this.med.push(v)
                            })
                        }

                    }
                    else {
                        const list = this.$store.state.upList
                        if(list){
                            o.ReturnData.forEach(v => {
                                list.forEach(a => {
                                    if(v.Name === a.Name && v.Brand === a.Brand && v.Manufacturer == a.Manufacturer){
                                        v.state = 'onUp'
                                    }
                                })
                            })
                        }
                        this.med = o.ReturnData
                    }

                })
            }
            else if(this.activeTab == 1){
                this.$post('Prescription/Prescription/GetAllPrescriptionOftenUsed',{
                    DoctorId:this.user.Data.User.DoctorId,
                    Name: this.search,
                    OrgId:this.user.Data.User.OrgId,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.pageShow = true
                    this.ListDetails = this.$store.state.upList
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }

                    if (h){
                        o.ReturnData.forEach(v => {
                            this.pres.push(v)
                        })
                    }
                    else {
                        this.pres = o.ReturnData
                    }
                })
            }else{
				this.$post('Prescription/Prescription/GetPrescriptionListByDoctorId',{
				    PatientId:this.Patient.PatientId,
					DoctorId:this.user.Data.User.DoctorId,
				    PageIndex: this.page,
				    PageSize: 15,
				}).then(o => {
					this.pageShow = true
				    if (!o.ReturnData || o.ReturnData.length < 15){
				            this.haveMore = false
				        }
				        else {
				            this.haveMore = true
				        }

				        if (h){
				            o.ReturnData.forEach(v => {
				                this.list.push(v)
				            })
				        }
				        else {
				            this.list = o.ReturnData
				        }
				})
			}
			this.pullByDrId()
        },
        pop(id,idx){
            this.delMedId = id
            this.delMedIdx = idx
            this.showConfirm2 = true
        },
        remove(i,idx){
            this.removeMedI = i
            this.removeMedIdx = idx
            this.showConfirm3 = true
        },
        confirm3(){
            this.ListDetails.forEach((v,k) => {
                if(v.Id == this.removeMedI.Id){
                    this.ListDetails.splice(k,1)
                }
            })
            this.med[this.removeMedIdx].state = null
        },
        popPre(id,idx){
            this.delPreId = id
            this.delPreIdx = idx
            this.showConfirm = true
        },
        toAdd(){
			this.$store.dispatch('pushIsFresh', false)
            const from = this.$route.query.from || 'um'
            this.$router.push(`/all-medicine?from=${from}`)
        },
        addToUp(i){
            this.$store.dispatch('pushcMed', i)
            this.$router.push('/consumption?back=-2')
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        Modify(i){
            if(this.$route.query.from === 'p'){
                return
            }
            else if(!i.IsActive){
                return
            }
            else{
                this.$store.dispatch('pushupList', [])
                this.$store.dispatch('pushpullFlag', 0)
                this.$router.push(`/usup-detail?id=${i.Id}`)
            }
        },
        chooseForPre(i){
            i.ListDetails.forEach((v,k) => {
                if(v.Invalid == 1){
                    i.ListDetails.splice(k,1)
                }
            })
			this.$store.dispatch('pushprimaryId', i.Id)

// 			this.$store.dispatch('pushpreNo',null)
// 			this.$store.dispatch('pushpullFlag', 0)
// 			this.$store.dispatch('pushDiagnoses2', i.Diagnoses)
// 			this.$store.dispatch('pushupList', i.ListDetails)
// 			this.$router.push(`/prescription?rejectId=${i.Id}`)
			this.$store.dispatch('pushupList', i.ListDetails)
			this.$store.dispatch('pushDiagnoses2', i.Diagnoses)

            this.$router.push(`/prescription?MedicationCompanyId=${i.MedicationCompanyId}`)
        },
        toList(i){
			if(!localStorage.getItem('searchName')){
				var searchArr = []
			}else{
				var searchArr = JSON.parse(localStorage.getItem('searchName'))
			}
			if(searchArr.indexOf(i) == -1){
				searchArr.unshift(i)
			}
			localStorage.setItem('searchName', JSON.stringify(searchArr))

            this.$store.dispatch('pushsearchText',i)
            if(this.$route.query.from == 'p'){
                this.$router.push(`/medicine-list?id=&from=ump`)
            }
            else if(this.$route.query.from == 'up'){
                this.$router.push(`/medicine-list?id=&from=upp`)
            }
            else{
                this.$router.push(`/medicine-list?id=&from=um`)
            }
        }
    }
}



